<template>
    <div class="metaData">
        <el-container style="height: 100%;">
            <el-aside width="274px" >
                <div class="table-header-btn">
                    <div class="table-header-btn-l">
                        <el-button type="primary" size="small" @click="addClick()">添加</el-button>
                        <el-button type="primary" size="small" @click="edikClick()">编辑</el-button>
                        <el-button type="danger" size="small" @click="deleteClick()" >删除</el-button>
                        <el-button type="primary" size="small" @click="sortClick()" >排序</el-button>
                    </div>
                </div>
                <!--<el-scrollbar :style="Height">-->
                  <div :style="{height:siteContentViewHeight-62+'px',overflow:'auto'}">
                    <metadataTree ref="moduleTree" :treeData="treeData" @node-click="orgTreeClick"></metadataTree>
                  </div>
                <!--</el-scrollbar>-->
            </el-aside>
            <el-main v-show="isBtn">
                <div class="table-header-btn">
                    <div class="table-header-btn-l"></div>
                    <div class="table-header-btn-r">
                        <el-button type="primary" size="small" @click="addListClick()">添加</el-button>
                        <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                        <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                        <el-button type="primary" size="small"  @click="saveMetaSort" >保存排序</el-button>
                        <el-button type="danger" size="small" @click="deleteListClick()" >删除</el-button>
                    </div>
                </div>
                <el-table
                    ref="multipleTable"
                    :data="tableData"
                    v-loading="tableDataLoading"
                    @selection-change="selectionChangeHandle"
                    :height="siteContentViewHeight - 52+'px'"
                >
                    <el-table-column type="selection" width="50" align="center"></el-table-column>
                    <el-table-column width="50" fixed type="index" :index="indexMethod" label="序号" align="center"></el-table-column>
                    <el-table-column prop="colCode" label="列名" ></el-table-column>
                    <el-table-column prop="colDatatype" label="数据类型"></el-table-column>
                    <el-table-column prop="colName" label="中文名" ></el-table-column>
                    <el-table-column prop="colDatalength" label="长度" ></el-table-column>
                    <el-table-column label="是否主键">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.colIspk == 1" size="small" type="danger">是</el-tag>
                            <el-tag v-else size="small">否</el-tag>
                        </template>
                    </el-table-column>
                    <!-- <el-table-column prop="menuName" label="显示格式" ></el-table-column>
                    <el-table-column prop="menuName" label="关联字典" ></el-table-column>
                    <el-table-column prop="menuName" label="默认值" ></el-table-column>
                    <el-table-column prop="menuName" label="是否可见" ></el-table-column>
                    <el-table-column prop="menuName" label="表单中可见" ></el-table-column>
                    <el-table-column prop="menuName" label="列表中可见 " ></el-table-column> -->
                    <el-table-column label="操作" width="100">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" plain @click="edikListClick(scope.$index,scope.row)">编辑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-main>
        </el-container>
        <!-- 左侧树、新增信息集目录 -->
        <el-dialog
            :title="title"
             width="50%"
            :visible.sync="addOneVisable"
            v-if="addOneVisable"
            :close-on-click-modal ="false"
            :modal-append-to-body='false'
            append-to-body
        >
            <add-one-name :checkedData="checkedData" :isEdit="isEdit" @addback="addback"></add-one-name>
        </el-dialog>
         <!-- 左侧树、新增信息集 -->
        <el-dialog
            :title="title"
            :visible.sync="addTwoVisable"
            width="50%"
            v-if="addTwoVisable"
            :close-on-click-modal ="false"
        >
            <add-two-name :checkedData="checkedData" :isEdit="isEdit" :edikGetDataList='edikGetDataList' @addback="addback"></add-two-name>
        </el-dialog>
        <!-- 左侧树、信息集排序 -->
         <el-dialog
            title="信息集类排序"
            :visible.sync="sortOneNameVisable"
            width="50%"
            v-if="sortOneNameVisable"
            @close='closeDialog'
        >
            <sort-one-name></sort-one-name>
        </el-dialog>
        <el-dialog
            title="信息集排序"
            :visible.sync="sortTwoNameVisable"
            width="50%"
            v-if="sortTwoNameVisable"
            @close='closeDialog'
        >
            <sort-two-name :checkedData="checkedData"></sort-two-name>
        </el-dialog>
        <!-- 右侧列表、新增-->
        <el-dialog
            :title="title"
            :visible.sync="addListVisable"
            width="50%"
            v-if="addListVisable"
            :close-on-click-modal ="false"
        >
            <add-cont-dialog :checkedData="checkedData" :isEdit="isEdit"  @addback="addback" :currentRow="currentRow"></add-cont-dialog>
        </el-dialog>

    </div>
</template>
<script>
import {swapItems} from '@/utils';
import metadataTree from "@/components/baseComponents/orgTree.vue";
import addOneName from "./add-one-name.vue";
import addTwoName from "./add-two-name.vue";
import sortOneName from "./sort-one-name.vue";
import sortTwoName from "./sort-two-name.vue";
import addContDialog from "./add-cont-dialog.vue";
export default {
    name: "metadata-manage-index",
    components: {
        metadataTree,
        addOneName,
        addTwoName,
        sortOneName,
        sortTwoName,
        addContDialog,
    },
    props:['siteContentViewHeight'],
    data() {
        return {
            Height: {
                height: document.documentElement.clientHeight - 135 + "px"
            },
            tableData:[],
            treeData:[],
            isEdit: '', //true 添加，false：修改
            addOneVisable:false,
            addTwoVisable:false,
            sortOneNameVisable:false,
            sortTwoNameVisable:false,
            addListVisable:false,
            tableDataLoading:false,
            currentRow:'',//选中的树数据
            edikGetDataList:{},//树结构、二级信息集编辑回显数据
            isParent:'',//树结构数据、true是一级、false是二级
            isBtn: false,//右侧列表按钮显示、隐藏
            tableDataSelections:[],
            selecteArr:[],//列表批量删除的集合
            tabCode:''//列表删除标识
        }
    },
    created(){
       this.getDataTree();
    },
    methods: {
        //获取机构
        getDataTree(){
            this.$http({
                url: this.$http.adornUrl(this.API.getDataTableTreeInfo),
                method: 'post',
                data: this.$http.adornData({})
            }).then(({data}) => {
                if(data && data.code===0){
                    this.treeData=data.data;
                    // this.orgTreeClick(this.treeData[0])
                }else{
                    this.$message.error(data.msg);
                    this.treeData=[];
                }
            });
        },
        //机构树点击事件
        orgTreeClick(data) {
            this.currentRow = data;
            this.isParent = data.isParent;
            if(this.isParent === false){
                this.getDataList();
                this.edikGetData();
                this.isBtn = true;
            }else if(this.isParent === true){
                this.isBtn = false;
            }
        },
        //点击系统获取数据
        getDataList(){
            this.tableDataLoading = true;
            this.$http({
                url: this.$http.adornUrl(this.API.getDataTableColumn),
                method: 'post',
                data: this.$http.adornData({
                    tabId:this.currentRow.id,
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.tableData=data.data;
                }else{
                    this.$message.error(data.msg);
                }
                this.tableDataLoading = false;
            });
        },

        //左侧树、添加信息集目录
        addClick(){
            if(this.isParent === false){
                this.$confirm('该目录下不允许添加！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            if(this.isParent === true){
                this.checkedData = this.currentRow;
                this.addTwoVisable = true;
                this.title ="信息集添加";
            }
            if(this.isParent === ""){
                this.checkedData = this.currentRow;
                this.addOneVisable = true;
                this.title ="信息集类添加";
            }
            this.isEdit =true;
        },

        //左侧树、编辑信息集目录
        edikClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            if(this.isParent === true){
                this.checkedData = this.currentRow;
                this.addOneVisable = true;
                this.title ="信息集类名称编辑";
            }
            if(this.isParent === false){
                this.checkedData = this.currentRow;
                this.edikGetDataList = this.edikGetDataList;
                this.addTwoVisable = true;
                this.title ="信息集编辑";
            }
            this.isEdit = false;
        },
        //信息集编辑回显数据
        edikGetData(){
            this.$http({
                url: this.$http.adornUrl(this.API.getDataTableInfo),
                method: 'post',
                data: this.$http.adornData({
                    tabId:this.currentRow.id
                })
            }).then(({data}) =>{
                if(data.code == 0) {
                    this.edikGetDataList = data.data[0];
                } else {
                    this.$message.error(data.msg);
                }
            })
        },

        //左侧树、信息集目录排序
        sortClick(){
            if(this.currentRow === null || this.currentRow === "") {
               this.sortOneNameVisable = true;
            }
            if(this.isParent === false){
               this.$confirm('该目录下无信息！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            if(this.isParent === true){
                this.checkedData = this.currentRow;
                this.sortTwoNameVisable = true;
            }
        },

        //左侧树、删除信息集目录
        deleteClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }

            if(this.isParent === true){
                this.$confirm('确定删除选中记录?', '操作提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http({
                        url: this.$http.adornUrl(this.API.deleteType),
                        method: 'post',
                        data: this.$http.adornData({
                            typeId:this.currentRow.id
                        })
                    }).then(({data}) =>{
                        if(data.code === 0) {
                            this.getDataTree();
                            this.$message.success('删除成功');
                        } else {
                            this.$message.error(data.msg);
                        }
                    })
                }).catch(() => {

                });
            }else if(this.isParent === false){
                this.$confirm('确定删除选中记录?', '操作提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http({
                        url: this.$http.adornUrl(this.API.deleteInfo),
                        method: 'post',
                        data: this.$http.adornData({
                            typeId:this.currentRow.id
                        })
                    }).then(({data}) =>{
                        if(data.code === 0) {
                            this.getDataTree();
                            this.$message.success('删除成功');
                        } else {
                            this.$message.error(data.msg);
                        }
                    })
                }).catch(() => {

                });
            }
        },

        //右侧列表添加
        addListClick(){
            this.checkedData = this.currentRow;
            this.title = '信息项添加';
            this.addListVisable = true;
            this.isEdit = true;
        },
        //右侧列表编辑
        edikListClick(index,row){
            this.checkedData = row;
            this.title = '信息项修改';
            this.addListVisable = true;
            this.isEdit = false;
        },
        //右侧列表删除
        deleteListClick(){
            if(this.selecteArr.length === 0) {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            };
            this.$confirm('确定删除选中记录?', '操作提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http({
                    url: this.$http.adornUrl(this.API.deleteDataTableCoulumns),
                    method: 'post',
                    data: this.$http.adornData({
                        data:JSON.stringify(this.selecteArr),
                        Id:this.currentRow.id
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.getDataList();
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            }).catch(() => {

            });
        },
        selectionChangeHandle(val) {//多选
            // var tempColId = '';
            // for(var i = 0; i < val.length; i++){
            //     var str = val[i].colId + ',';
            //     tempColId = str + tempColId
            // }
            // this.selecteId = tempColId;

            this.tableDataSelections = val;
            this.tabCode = val[0].tabCode;

            this.selecteArr=[];
            this.tableDataSelections.forEach((item,index)=>{
                this.selecteArr.push({
                    id:item.colId,
                    code:item.colCode
                })
            })
        },
        moveDirection(direction){ //移动
            if(this.tableDataSelections.length === 0) {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            };
            var that=this,index;
            that.tableDataSelections.sort((prev,next)=>{
                return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
            })
            if(direction=='down') that.tableDataSelections.reverse()
            that.tableDataSelections.forEach(item=>{
                index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                if(direction=='up') that.moveUp(index);
                else if(direction=='down') that.moveDown(index)
            })
        },
        moveUp(index){//上移
            if(index==0) return;
            this.tableData=swapItems(this.tableData,index,index-1,'up');
        },
        moveDown(index){ //下移
            if(index==this.tableData.length-1) return;
            this.tableData=swapItems(this.tableData,index,index+1,'down');
        },
        saveMetaSort(){//保存排序
            var sortArr=[];
            if(this.tableData.length<0) return;
            this.tableData.forEach((item,index)=>{
                sortArr.push({
                    id:item.colId,
                    orderno:index+1
                })
            })
            this.$http({
                url: this.$http.adornUrl(this.API.dataTableColumnSortColumn),
                method: 'post',
                data: this.$http.adornData({
                    data:JSON.stringify(sortArr)
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.getDataList()
                    this.$message.success('操作成功');
                }else{
                    this.$message.error(data.msg);
                }
            });
        },
        indexMethod(index) {
            return index + 1;
        },
        //树机构排序关闭回调
        closeDialog() {
           this.getDataTree()
        },
        // 添加或修改组件回调方法--子组件回调数据的方法
	    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
        addback(isRefresh){
            this.addOneVisable = false;
            this.addTwoVisable = false;
            this.addListVisable = false;
            console.log(this.addListVisable)
            if(isRefresh){
                this.getDataTree();
                this.getDataList();
            }
        },
    },
}
</script>
<style lang="scss" scoped>
    .el-aside {
        border-right: 1px solid #dcdfe6;
        padding-top: 10px;
    }
    .el-main {
        padding:0;
    }
    .el-form-item {
         margin-bottom: 0px;
    }
</style>



